  3% {
    width: 75%;
  }
  5% {
    width: 90%;
  }
  10% {
    width: 95%;
  }
  100% {
    width: 100%;
  }
}
body {
  min-height: 5000vh;
}
body > joda-content {
  opacity: 0;
}
body.loaded {
  min-height: unset;
}
body.loaded > joda-content {
  opacity: 1;
}

body > .loader {
  position: fixed;
  z-index: 99;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  display: flex !important;
  align-items: center;
  align-content: center;
  justify-content: center;
  opacity: 1;
  flex-wrap: wrap;
  flex-direction: column;
}
body > .loader > .loader-bar {
  position: relative;
  display: block;
  background-color: lightgrey;
  height: 4px;
  width: 70vw;
  max-width: 700px;
  top: 0;
  z-index: 1;
  margin-top: 15vh;
}
body > .loader > .loader-bar::before {
  content: "";
  font-family: sans-serif;
  font-size: 12px;
  line-height: 10px;
  position: relative;
  text-align: center;
  color: white;
  z-index: 99;
  -webkit-animation: leu--site-loader 60s ease-out;
  animation: leu--site-loader 30s ease-out;
  display: block;
  background-color: indianred;
  height: 100%;
  width: 35%;
  top: 0;
}
body > .loader > .loader-img > img {
  flex-direction: column;
  display: block !important;
  height: auto !important;
  min-width: 150px;
  width: 15vw;
  max-width: 550px !important;
}

body.loaded > .loader {
  opacity: 0;
  z-index: -99;
