.isl-navbar-switch1.mobile .isl-navbar-switch1__nav {
  height: auto;
  padding: 15px 20px;
}
.isl-navbar-switch1.mobile .isl-navbar-switch1__nav--inner {
  justify-content: unset;
  padding-left: unset;
  padding-right: unset;
}
.isl-navbar-switch1.mobile .isl-navbar-switch1__nav--items {
  display: none;
}
.isl-navbar-switch1 aside > .backdrop {
  position: fixed;
  background-color: #212529;
  opacity: 0.4;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  transform: scale(0%);
  -webkit-transform: scale(0, 0);
  transition: all 0.1s ease-in-out;
  z-index: 9998;
}
.isl-navbar-switch1 aside > .menu {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  width: 90vw;
  max-width: 300px;
  z-index: 9999;
  transform: translateX(100%);
  background-color: #212529;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  transition: all 0.2s ease-in-out;
}
.isl-navbar-switch1 aside > .menu ul {
  padding: 0;
  margin: 0;
}
.isl-navbar-switch1 aside > .menu ul > li {
  list-style: none;
  width: 100%;
}
.isl-navbar-switch1 aside > .menu ul > li > a {
  display: block;
  text-decoration: none;
  width: 100%;
  padding: 15px;
  color: #fff;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}
.isl-navbar-switch1 aside > .menu ul > li > a:hover {
  background-color: #41C7A8;
}
.isl-navbar-switch1 aside > .menu > .menu-header {
  width: 100%;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  display: flex;
  align-content: center;
  justify-content: end;
}
.isl-navbar-switch1 aside > .menu > .menu-header > svg {
  cursor: pointer;
  margin: 20px;
  color: #fff;
  height: 38px;
  width: 38px;
}
.isl-navbar-switch1.sidebar aside > .backdrop {
  transform: scale(100%);
  -webkit-transform: scale(100%, 100%);
}
.isl-navbar-switch1.sidebar aside > .menu {
  transform: translateX(0%);
}

joda-content {
  display: none;
  --joda-init: true;
}

joda-content.loaded {
  display: block;
}

.slideshow {
  position: relative;
}
.slideshow img {
  opacity: 0;
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100%;
  transition: opacity 1s ease-in-out;
}
.slideshow img:first-of-type {
  opacity: 1;
}
.slideshow img.show {
  opacity: 1 !important;
  transition: opacity 1s ease-in-out;
}

@keyframes liscom-blink {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}
typewriter-element .cursor {
  animation: liscom-blink 1s step-end infinite;
}

liscom-slider {
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* Internet Explorer 10+ */
  scroll-snap-type: both mandatory;
  scroll-snap-stop: normal;
  scroll-behavior: smooth;
  position: relative;
}
liscom-slider:-webkit-scrollbar {
  width: 0;
  height: 0;
}
liscom-slider > slot > * {
  scroll-snap-align: start;
}
liscom-slider .liscom-slider__indicator {
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  bottom: 0;
}

liscom-scroll-to-top {
  opacity: 0;
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  z-index: 99;
  border: none;
  outline: none;
  background-color: rgba(0, 0, 0, 0.1);
  box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 5px;
  color: white;
  cursor: pointer;
  padding: 10px;
  border-radius: 4px;
  display: flex;
  align-content: center;
  align-items: center;
  text-align: center;
  justify-content: center;
  width: 2.8rem;
  height: 2.8rem;
  transition: opacity 0.5s ease-in-out, background-color 0.2s ease-in-out;
}
liscom-scroll-to-top:hover {
  background-color: rgba(0, 0, 0, 0.2);
}
liscom-scroll-to-top.show {
  opacity: 1;
}
liscom-scroll-to-top * {
  color: white;
